@import "../main.scss";

.wrapper {
  position: absolute;
  height: 48vh;
  width: 22.4vw;
  left: 1vw;
  bottom: 2vh;
  background-color: $darkColor;
}

.header {
  position: absolute;
  font-family: "Almoni Regular";
  font-size: 24pt;
  color: $lightColor;
  left: 16px;
  top: 16px;
}

.iconContainer {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 1vw;
  top: 16px;
  width: 24pt;
  height: 24pt;
}

.play {
  position: relative;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 15px solid rgba($lightColor, 0.5);
  transition: border 0.2s ease-out;
  &:hover {
    border-left: 15px solid $lightColor;
  }
}

.stop {
  position: relative;
  width: 5px;
  height: 15px;
  border-right: 5px solid rgba($lightColor, 0.5);
  border-left: 5px solid rgba($lightColor, 0.5);
  transition: border 0.2s ease-out;
  &:hover {
    border-color: $lightColor;
  }
}

wave {
  position: absolute;
  top: 70px;
}

.songsContainer {
  position: absolute;
  top: 160px;
  overflow-y: scroll;
  height: 28.5vh;
  font-family: "Almoni Light";
  font-size: 12pt;
}

.songContainer {
  color: $lightColor;
  display: flex;
  position: relative;
  justify-content: space-between;
  direction: ltr;
  border-bottom: 0.5px solid $lightColor;
  padding: 5px;
  transition: background-color 0.25s ease-out;
  cursor: pointer;
  &:hover {
    background-color: rgba($lightColor, 0.5);
  }
}

.songActive {
  background-color: $lightColor;
  color: $darkColor;
}
