@import "../main.scss";

.button {
  position: relative;
  height: 5px;
  width: 5px;
  border-radius: 5px;
  border: 1pt solid rgba($lightColor, 0.5);
  transition: background-color 0.2s ease;
}

.active {
  background-color: $lightColor;
}

.notActive {
  background-color: $darkColor;
  &:hover {
    background-color: rgba($lightColor, 0.5);
  }
}

.box {
  position: relative;
  height: 18px;
  width: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  cursor: pointer;
}
