@import "../main.scss";

.return {
  position: absolute;
  background-color: $darkColor;
  cursor: pointer;
  width: 30px;
  height: 30px;
  top: 40px;
  z-index: 2;
}

.slesh {
  position: absolute;
  bottom: 40%;
  left: 0;
  width: 100%;
  height: 3px;
  border-radius: 1.5px;
  background-color: rgba($lightColor, 0.5);
  transition: background-color 0.2s ease-out;
}
