@import "../main.scss";

.wrapper {
  position: absolute;
  height: 46vh;
  width: 20vw;
  right: 1vw;
  top: 2vh;
  background-color: $darkColor;
  font-family: "Almoni Regular";
}

.header {
  position: absolute;
  font-family: "Almoni Regular";
  font-size: 24pt;
  color: $lightColor;
  left: 16px;
  top: 16px;
}

.pie {
  position: absolute;
  width: 80%;
  left: 50%;
  bottom: 45%;
  transform: translate(-50%, 50%);
}
