@import "../main.scss";

.wrapper {
  position: absolute;
  height: 48vh;
  width: 34.6vw;
  right: 1vw;
  bottom: 2vh;
  background-color: $darkColor;
}

.header {
  position: absolute;
  font-family: "Almoni Regular";
  font-size: 24pt;
  color: $lightColor;
  left: 16px;
  top: 16px;
}

.description {
  position: absolute;
  font-family: "Almoni Light";
  font-size: 12pt;
  color: $lightColor;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  transition: opacity 0.2s ease-out;
}

.mapContainer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 82%;
  background-color: $darkColor;
}
:focus {
  outline: none;
  border: none;
}
.rsm_geography {
  display: inline-block;
  vertical-align: middle;
  fill: $darkColor;
  stroke: $lightColor;
  stroke-width: 0.2;
}

.dot {
  fill: rgba($lightColor, 0.5);
  transition: fill 0.25s ease-out, opacity 0.25s ease-out;
  &:hover {
    fill: $lightColor;
  }
}

.zoomIcon {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 25px;
  cursor: pointer;
}

.horizontalStripe {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 4px;
  border-radius: 2px;
  background-color: rgba($lightColor, 0.5);
  transition: background-color 0.2s ease-out;
}

.verticalStripe {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 100%;
  border-radius: 2px;
  background-color: rgba($lightColor, 0.5);
  transition: background-color 0.2s ease-out;
}
