@import "../main.scss";

.wrapper {
  position: absolute;
  height: 46vh;
  width: 37vw;
  left: 1vw;
  top: 2vh;
  background-color: $darkColor;
}

.header {
  position: absolute;
  font-family: "Almoni Regular";
  font-size: 24pt;
  color: $lightColor;
  left: 16px;
  top: 16px;
}

.horizontalLine {
  position: absolute;
  height: 2px;
  width: 90%;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  background-color: $lightColor;
}
