@import "../main.scss";

.icon {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  bottom: 60px;
  transform: translate(-50%, 50%);
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
}

.iconImage {
  position: relative;
  mask-repeat: no-repeat;
  mask-size: contain;
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
}

.nonActiveIcon {
  background-color: $darkColor;
  &:hover {
    background-color: rgb(119, 119, 132);
  }
}

.activeIcon {
  background-color: $lightColor;
}

.arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 20px solid rgba($lightColor, 0.5);
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  transition: border 0.2s ease-out;
  bottom: 92px;
  transform: translateX(-50%);
}

.contentBox {
  position: absolute;
  width: 80%;
  height: 50%;
  bottom: 112px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba($lightColor, 0.5);
}

.content {
  padding: 5%;
  line-height: 16pt;
}

.title {
  font-family: "Almoni Black";
  font-size: 16pt;
}

.desc {
  font-family: "Almoni Regular";
  font-size: 12pt;
  white-space: pre-wrap;
}

.time {
  font-family: "Almoni Black";
  font-size: 16pt;
  position: absolute;
  bottom: 10px;
  right: 14px;
}
